<template>
  <div id="app">
    <div id="nav">
      <router-link to="/">Home</router-link> |
      <router-link to="/about">About</router-link>
    </div>
    <router-view />
    <router-link to="/create" :class="addStyle" v-if="!isCreate">
      <i class="material-icons">add</i>
    </router-link>
    <router-link to="/" :class="addStyle" v-if="isCreate">
      <i class="material-icons">chevron_left</i>
    </router-link>

  </div>
</template>

<script>
export default {
  data() {
    return {
      addStyle:
        "btn-floating btn-large waves-effect waves-light red add-note pulse",
      isCreate: false
    };
  },
  mounted() {
    this.isCreate = this.$route.name == "create";
    setTimeout(() => {
      this.addStyle =
        "btn-floating btn-large waves-effect waves-light red add-note";
    }, 1000);
  },
  watch: {
    $route(to) {
      this.isCreate = to.name == "create";
    }
  }
};
</script>

<style>
.add-note {
  position: fixed;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
}
body {
  min-height: 100vh;
}
</style>
