<template>
  <router-link class="collection-item avatar" to="/">
    <img :src="pickIcon(data.labels)" class="circle" />
    <span class="title">{{ data.properties.name }}</span
    ><br />
    <span v-if="data.labels.length">
      <router-link to="/about"
        >#{{ data.labels[0] }}
      </router-link>
    </span>
  </router-link>
</template>

<script>
export default {
  name: "CollectionItem",
  props: ["data"],
  data() {
    return {
      icons: {
        Person: "https://fonts.gstatic.com/s/i/materialicons/face/v1/24px.svg"
      }
    };
  },
  methods: {
    pickIcon(label) {
      if (label.length && label[0] in this.icons) {
        return this.icons[label[0]];
      }
      return "https://fonts.gstatic.com/s/i/materialicons/folder/v1/24px.svg";
    }
  }
};
</script>
